<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" srcset="img/phone-square-3@2x.jpg 2x" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
      {{ item.product.id }}
    </span>

    <ProductCounter class="product__counter cart-item__counter" :amount.sync="amount" />

    <b class="product__price">
      {{ item.amount * item.product.price | numberFormat }}
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"  @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import ProductCounter from '@/components/ProductCounter.vue';

export default {
  filters: { numberFormat },
  components: { ProductCounter },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    // ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
    ...mapActions(['deleteCartProduct']),
    deleteProduct(value) {
      this.deleteCartProduct({ productId: value });
    },

  },
};
</script>
